'use client';

import { supabase } from '@/lib/supabase';
import { Button } from '@/components/ui/button';
import { useEffect, useState } from 'react';
import { useRouter } from 'next/navigation';

export default function LoginPage() {
    const router = useRouter();
    const [loading, setLoading] = useState(false);

    useEffect(() => {
        const checkUser = async () => {
            const { data: { session } } = await supabase.auth.getSession();
            if (session) {
                router.push('/');
            }
        };
        checkUser();
    }, [router]);

    const handleLogin = async () => {
        setLoading(true);
        const { error } = await supabase.auth.signInWithOAuth({
            provider: 'google',
            options: {
                redirectTo: window.location.origin,
            },
        });
        if (error) {
            console.error('Login error:', error);
            setLoading(false);
        }
    };

    return (
        <div className="min-h-screen flex flex-col items-center justify-center p-6 bg-background">
            <div className="w-full max-w-sm space-y-8 text-center">
                <div className="space-y-2">
                    <h1 className="text-2xl font-bold tracking-tight py-4">Untld</h1>
                    <p className="text-muted-foreground text-sm uppercase tracking-widest">
                        A minimal space for your bookmarks
                    </p>
                </div>

                <div className="pt-8">
                    <Button
                        onClick={handleLogin}
                        disabled={loading}
                        className="w-full h-12 rounded-full font-bold uppercase tracking-wider text-[11px] shadow-lg hover:shadow-xl transition-all"
                    >
                        {loading ? 'Initializing...' : 'Continue with Google'}
                    </Button>
                </div>
            </div>
        </div>
    );
}
